import React from "react";
import Logo from "../../../img/devjane.png";
function Header() {
  const handleClick = () => {
    document.body.classList.toggle("nav-open");
  };

  return (
    <header>
      <div className="logo">
        <img src={Logo} alt="" />
      </div>
      <button
        onClick={handleClick}
        className="nav-toggle"
        aria-label="toggle navigation"
      >
        <span className="hamburger"></span>
      </button>
      <nav className="nav">
        <ul className="nav__list">
          <li className="nav__item">
            <a onClick={handleClick} href="#home" className="nav__link">
              Home
            </a>
          </li>
          <li className="nav__item">
            <a onClick={handleClick} href="#services" className="nav__link">
              My Services
            </a>
          </li>
          <li className="nav__item">
            <a onClick={handleClick} href="#about" className="nav__link">
              About me
            </a>
          </li>
          <li className="nav__item">
            <a onClick={handleClick} href="#work" className="nav__link">
              My Work
            </a>
          </li>
        </ul>
      </nav>
    </header>
  );
}

export default Header;
